/*=== 4.1 FOOTER TOP ===*/
footer{
    &.section-padding{
        padding-bottom:0; 
    }
    .logo-footer {
        margin-bottom: 20px;
        .fill-color{
            fill: $white;
        }
    }
    
    .single-footer{
        margin-bottom: 30px;
        &:last-child{
            margin-bottom: 0;
        }
        @include tablet{
            
            margin-bottom:0;
        }
        p{
            color: $white-color;
        }
    }
    
    ul{
        li{
            position: relative;
            padding-left: 25px;
            margin-bottom: 15px;
            color: $white-color;
            i.fa{
                position: absolute;
                left: 0;
                top: 4px;
                color: $cornflower-blue;
            }
        }
    }
    ul.footer-links{
        li{
            padding-left: 15px;
            margin-bottom: 18px;
            &:hover{
                a{
                    color:$primary-color;
                }
            }
            a{
                color: $white-color;
            }
        }
    }
    h3.footer-title{
        color: $white;
        margin-top: 0;
        margin-bottom: 30px;
        
    }
    .instagram {
        ul {
          padding: 0 10px;
        }
        li {
          width: auto;
          overflow: hidden; 
           padding: 10px 5px 0;
           margin-bottom:0;     
          img {
            width: 100%;
            height: 100%;
            transition: .4s;
          }
        }
      }
      form{
        input{
            width: 100%;
            color: $white;
            margin-bottom: 15px;
            padding: 15px 20px;
            border: 1px solid #37383b;
            background: $black-russian;
            font-family: 'Open Sans', sans-serif;
            &::-webkit-input-placeholder{
                color: $white;
            }
        }
        .common-btn{
            display: block;
            text-align: center;
        }
    }
}

/*=== 4.2 COPYRIGHT ===*/

.copyright{
    margin-top: 15px;
    padding-top:30px;
    padding-bottom: 30px;
    @include tablet{
      padding-bottom: 5px;
      margin-top: 40px;
    }
    p{
      text-align: center;
      color: $white-color;
      @include tablet{
        text-align: left;
      }
        a{
            color: $primary-color;
        }
    }
    &.elements-copyright{
        p{
            text-align: center;
        }
    }
    .footer-social{
        text-align: center;
        @include tablet{
          text-align: right;
        }
        a{
            color: $white-color;
            margin: 0 10px;
            &:last-child{
                margin-right:0; 
            }
            &:hover{
                color:$primary-color;
            }
        }
    } 
}
.element-footer{
    .copyright{
        padding-top:0;
        margin-top: 15px; 
    }
}