.card.team-card {
    margin-bottom: 27px;
    @include tablet{
        margin-bottom: 0;
    }
	img {
		@include transition(all 0.4s ease);
    }
    a{
        color: $black
    }
	&:hover {
		img {
			// filter: blur(2px);
			@include transform(scale(1.1));
        }
        a{
            color: $primary-color;
        }
	}
	.hover-overlay { 
        @include desktops{
            flex-direction: column;
        }
        @include large-desktops{
            flex-direction: row;
        }
		.overlay-content {
			text-align: center;
			color: #ffffff;
			font-weight: 300;
			&.diamond-shape {
				max-width: 100px;
				@include transform(rotate(45deg));
            }
            &.social-icons{
                position: absolute;
                bottom: auto;
                left: 50%;
                transform:translateX(-50%);
                width: 100%;
                @include large-desktops{
                    bottom:10%;
                }
                a{
                    color: $white;
                    border: 1px solid $white;
                    text-align: center;
                    transition: .4s;
                    &:hover{
                        background-color: $white;
                        color: $black;
                    }
                }
            }
        }
        
		.icon-circle {
			margin: 0 5px;
			width: 50px;
			height: 50px;
            line-height: 50px;
            @include tablet{
                width: 40px;
                height: 40px;
                line-height: 40px;
            }
            @include large-desktops{
                width: 50px;
                height: 50px;
                line-height: 50px;
            }
		}
		.icon-ract.ract45 {
			border: 1px solid $white-color;
			margin: 3px 1px;
		}
		.ract45 {
			@include transform(rotate(0deg));
		}
	}
}
.card-title {
    font-family: 'PT Serif',serif;
    
}
.secondary-tittle {
    font-family: 'Open Sans', sans-serif;
    font-style: italic;
}

.social-icons{
    display: flex;
    a{
        width: 50px;
        height: 50px;
        line-height: 50px;
        border-radius: 50%;
        color: $white;
        border: 1px solid $white;
        text-align: center;
        margin: 0 5px;
        transition: .4s;
        &:hover{
            background-color: $white;
            color: $black;
        }
        i.fa{
            font-weight: 700;
        }
    }
}