/* ================================================
--> Elements carousel style
================================================== */
.carousel-style-one{
	.carousel-item {
		height: 300px;
		@include tablet {
			height: 350px;
			padding: 0 50px;
		}
		@include desktops {
			height: 400px;
		}
	}
	.carousel-description {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
		h1 {
			font-size: 25px;
			font-weight: 700;
			text-transform: uppercase;
			color: $white-color;
			line-height: 35px;
			@include tablet {
				font-size: 35px;
				line-height: 55px;
			}
			@include desktops {
				font-size: 40px;
				line-height: 55px;
			}
		}
		p {
			font-family: 'Open Sans', sans-serif;
			font-size: 14px;
			color: $white-color;
			padding:  2px 0 10px;
			line-height: 24px;
			@include tablet {
				font-size: 16px;
				line-height: 26px;
				padding: 25px 0 30px;
			}
			@include desktops {
				padding: 35px 0 40px;
			}
		}
		.slider-btn{
            .common-btn{
                padding:0 13px;
                font-size: 10px;
                font-weight: bold;
                height: 30px;
                line-height: 30px;
                @include tablet{
                height: 50px;
                padding:0 40px;
                line-height: 50px;
                font-size: 14px;
                }
            }
        }
	}
	.owl-dots {
        position: absolute;
        bottom: 5%;
        left: 50%;
		display: inline-flex;
		transform: translate(-50%);
        .owl-dot {
            height: 15px;
            width: 15px;
            background: transparent;
            margin: 0 5px;
            border-radius: 50%;
            border: 4px solid #fff;
            position: relative;
            &.active{
				height: 20px;
				width: 20px;
				background: #3a6ee9;
				top: -2.5px;
				border-width: 5px;
            }
        }    
    }
}

// Service Carousel =====================================
.display-single_element{
    .service-carousel{
        .owl-nav {
            .owl-next,.owl-prev{
                color: #4e5055;
            }
        }   
    }
}
.service-carousel{
    .owl-nav {
        position: absolute;
        bottom: -30%;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        .owl-next,.owl-prev{
            font-size: 20px;
            font-weight: 700;
            width: 40px;
            height: 40px;
            line-height: 35px;
            text-align: center;
            border: 2px solid #4e5055;
            border-radius: 50%;
            color: #e1e1e2;
            cursor: pointer;
            transition: .4s;
            margin: 0 5px;
            &:hover{
                border-color:$primary-color;
                color: $primary-color;
            }
        }
    }
}

// TESTIMONIAL Carousel =====================================
.testimonial-carousel{
    padding-top: 185px;
    margin-top: 20px;
    position: relative;
    @include tablet{
        padding-top: 120px;
    }
    p{
        padding-bottom: 13px;
    }
    .owl-dots {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        @include tablet{
            display: flex;
        }
        .owl-dot{
            position: absolute;
            display: none;
            visibility: hidden;
            height: 100px;
            width: 100px;
            transform: translateX(-50%);
            border-radius: 50%;
            overflow: hidden;
            transition: .4s;
            @include tablet{
                margin: 0 10px;
                position: relative;
                transform: translateX(0%);
                height: 100%;
                width: 100%;
                display: block;
                visibility: visible;
            }
            &::before{
               @include tablet{
                background: #162632;
                position: absolute;
                content: "";
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                opacity: .40;
                transition: .3s;
               }
            }
            &.active {
                display: block;
                visibility: visible;
                @include tablet{
                    transform: scale(1.2);
                // overflow: hidden;
                &::before{
                    opacity: 0;
                }
                }
            }
            img{
                display: block;
                width: 100%;
                @include desktops{
                    width: auto;
                }
            } 
        }
    }

    .owl-nav {
        position: absolute;
        top: 30%;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        @include tablet{
            width: 100%;
            left: 0;
            top: 50%;
            transform: translateX(0);
        }

        .owl-next,.owl-prev{
            position: relative;
            left: 0;
            font-size: 30px;
            padding: 7px;
            color: #d9d9d9;
            transition: .4s;
            @include tablet{
                left: -30%;
                position: absolute;
				font-size: 30px;
            }
            @include desktops{
                left: -15%;
            }
            &:hover{
                color: $primary-color;
            }
            &::before{
                content: '|';
                right: 0;
                position: absolute;
            }
        }
        .owl-next{
            @include tablet{
                left: auto;
                right:-30%;
            }
            @include desktops{
                left: auto;
                right: -15%;
            }
            &::before{
                left: 0;
                right: auto;
                
            }
        }
    }
	h4.client-name {
		font-size: 20px;
		margin-bottom: 10px;
		font-weight: 600;
	}
	span.client-status{
		color: $primary-color;
		font-style: italic;
	}
}

// Brand Carousel using slick ===================================
.brand-carousel{
    display: flex;
    .single-brand{
        img{
            width: auto;
            margin: 0 auto;
        }
    }
}


// image Carousel using slick ===================================
.image_carousel {
	.element_row {
		margin: 0 -15px;
		.image_single {
			padding: 0 15px;
		}
	}
}
