/* ================================================
--> Elements Bredcrumb style
================================================== */
.bredcrumb {
	opacity: 1;
	position: relative;
	z-index: 1;
	&.gradient-overlay:before {
		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#202125+0,202125+100&0.95+0,0.1+40 */
		background: -moz-linear-gradient(top, rgba(32,33,37,0.95) 0%, rgba(32,33,37,0.1) 40%, rgba(32,33,37,0.1) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top, rgba(32,33,37,0.95) 0%,rgba(32,33,37,0.1) 40%,rgba(32,33,37,0.1) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom, rgba(32,33,37,0.95) 0%,rgba(32,33,37,0.1) 40%,rgba(32,33,37,0.1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2202125', endColorstr='#1a202125',GradientType=0 ); /* IE6-9 */
	}
	&.bg-image:after {
		background-color: #212121;
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		opacity: .77;
		z-index: -1;
	}
}
.bredcrumb-inner {
	height: 240px;
    padding-top: 65px;
	@include tablet {
		height: 250px;
	}
	@include desktops {
		height: 400px;
	}
	ul {
		position: relative;
		li, a {
			position: relative;
		    display: inline-block;
		    font-size: 13px;
		    color: $white;
		    padding: 5px 7px;
		    @include tablet {
		    	font-size: 15px;
			}
			&.active{
				color: $primary-color;
			}
		}
		li a {
			opacity: 0.7;
		}
		li:after {
		    position: absolute;
		    content: '\f105';
		    font-family: FontAwesome;
		    top: 10px;
		    right: 0;
		    opacity: 0.7;
		}
		li:last-child:after {
		    display: none;
		}
		li:first-child {
			padding-left: 0px;
		    a {
		    	padding-left: 0px;
		    }
		}
		&.pager {
			text-align: left;
			@include tablet {
				text-align: right;
			}
		}
	}
	h2 {
		color: $white;
		font-size: 28px;
		line-height: 45px;
		text-transform: capitalize;
		@include tablet {
			font-size: 40px;
		}
	}
	p {
		color: $white;
		line-height: 1.5;
		@include desktops {
			line-height: 1;
		}
	}
}
.display-single_element {
	.bredcrumb {
		h2 {
			padding-top: 0px!important;
		}
	}
}

	 .bredcrumb{
		 &.bg-image {
			background-image: url(../img/about-us/backgrounds/services-area-bg.jpg)
	 	}
	 }

.element-top.bredcrumb {
	padding: 30px 0 20px;
	@include tablet{
		padding: 30px 0;
	}
    
}