.cta-section{
    &.bg-image{
        background-image: url(../img/home-page/backgrounds/cta-bg.jpg);
        background-position: calc(70%);
        @include tablet{
            background-position: top center;
        }
    }
}
.cta-content{
    h2{
        font: 700 38px 'PT Serif', serif;
        color: $white;
        margin-bottom: 20px;
        @include tablet{
            font: 700 55px 'PT Serif', serif;
        }
    }
    h4 {
        font: 400 17px 'Open Sans', sans-serif;
        color: #fff;
        margin-bottom: 50px;
    }
}