/*
      ### HOME CAROUSEL
*/
.home-carousel.carousel-style-one{
    .carousel-item{
        height: 255px;
        padding: 0;
        @include tablet{
            height: 100vh;
         }
         @include desktops{
            height: 100vh;
        }
        .carousel-description {
            padding-top:80px;
            padding-bottom: 10px; 
            @include desktops{
                padding-top:120px;
                padding-bottom: 0;
            }
        }
        h1{
            @include tablet{
                font-size: 65px;
                line-height: 70px;
            }
        }
        p{
            font-size: 11px;
            line-height: 9.95px;
            padding: 10px 0 3px;
            @include tablet{
                font-size: 26px;
                padding: 30px 0 50px;
            }
        }
    }
}

.home-service{
    &.section-padding{
        padding-bottom: 140px;
    }
}

.home-news{
    &.section-padding{
        padding-bottom: 50px;
        @include tablet{
            padding-bottom: 10px;
        }
        @include desktops{
            padding-bottom: 130px;
        }
    }
}

