/* Single Service */
.info.service-info{
    min-height: 260px;
    transition: .4s;
    &:hover{
        background: $primary-color;
        color: $white-color;
        .fill-color{
            fill: $white-color;
        } 
        .description{
            p,h3{
                color: $white-color;
            }
        }
    }
}
    .info{
        color: $white;
        text-align: center;
        padding: 35px 10px 25px;
        position: relative;
        overflow: hidden;
        z-index: 1;
        min-height: 250px;
        transition: .4s;
        .icon{
            padding-bottom:18px;
            .fill-color{
                fill: $primary-color;
                transition: .4s;
            } 
        }
        .description{
            h3{
                font-family: 'PT Serif', serif;
                padding-bottom:20px;
                color: $black;
                transition: .3s;
            }
            p{
                font-family: 'Open Sans', sans-serif;
                color: #848484;
                transition: .4s;
            }
            &.color-white{
                h3{
                    color: $white;
                }
                p{
                    color: #848484;
                }
            }
        }
        &.service-margin-bottom {
            margin-bottom: 30px;
        }
    }
