ul.feature-list{
    position: relative;
    display: inline-block;
    &.two-columns{ 
        li{
            width: 100%; 
            @include desktops{
                float: left;
                width: 50%;
            }
        }
    }
    li{
        margin: 7px 0;
    }
    
    i.fa {
        margin-right: 10px;
        color: $primary-color;
    }
}

