/*=== VARIABLES ===*/
$blue:#3a6ee9;
$white: #fff;
$ghost-white:#F5F5F7;
$black: #222222;
$ebony :#2F3238;
$black:#222222;
$black-russian: #202125;
$cornflower-blue:#5B8AF8;
