/* ================================================
--> Elements video style
================================================== */
.promo-video{
	position: relative;
	width: 100%;
	float: none;
	overflow: hidden;
	max-height: 400px;
	padding-top: 100%;
	&:before {
		background-color: rgba(10,35,59,0);
	}
	@include tablet {
		max-height: 500px;
		width: 100%;
		position: relative;
		margin: 0px;
		padding-top: 60%;
	}
	@include desktops {
		padding-top: 40%;
	}
	.video-button{
		position: absolute;
		left: 0;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		i {
			position: relative;
			height: 80px;
			width: 80px;
			line-height: 80px;
			font-size: 35px;
			border-radius: 50%;
			display: inline-block;
			color: $white-color;
			padding-left: 10px;
			text-align: center;
			border: 2px solid $white-color;
			@include transition(transform 0.3s ease);
		}
		&:hover {
			i {
				@include transform(scale(1.1));
			}
		}
	}
}
.video-box{
	a{
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		iframe{
			width: 100%;
			height: 100%;
			border: none;
			position: relative;
		}
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			
		}
		img {
			position: relative;
			@include transition(transform 0.7s ease);
		}
		&:hover {
			img {
				@include transform(scale(1.1))
			}
		}
	}
}

.fluid-width-video-wrapper {
	width: 100%;
	position: relative;
	padding: 0;
	iframe, object, embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border: 0px;
	}
}

// Modal Video with fancybox
.promo-video-holder {
	display: none;
	@include tablet {
		display: block;
	}
	.promo-video {
		float: none;
		padding-top: 0;
	}
	a.video-link {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		i {
			position: relative;
			height: 80px;
			width: 80px;
			line-height: 80px;
			font-size: 35px;
			border-radius: 50%;
			display: inline-block;
			color: $white-color;
			padding-left: 10px;
			text-align: center;
			border: 2px solid $white-color;
			@include transition(transform 0.3s ease);
		}
		&:hover {
			i {
				@include transform(scale(1.1));
			}
		}
	}
}