/*==================================
card
====================================*/
#cards_element {
	.card {
		margin-bottom: 30px;
		.display-single_element {
			p {
				margin-bottom: -10px;
			}
		}
	}
}
.card {
	border: 0px;
	border-radius: 0px;
	// background-color: transparent;
	overflow: hidden;
	.card_img {
		position: relative;
		width: 100%;
		overflow: hidden;
	}
	.card-img {
		border-radius: 0px;
	}
	.card-title {
		margin: 0px;
		padding: 15px 0 10px;
		@include transition(all 0.3s ease);
		&:hover {
			color: $primary-color;
		}
	}
	.card-block {
		padding: 10px 0 0;
		p.card-text {
			padding: 15px 0px;
		}
	}

	&.img-mid {
		.card_img {
			padding: 20px 0 20px;
		}
		.card-title {
			padding-top: 0px;
		}
		.card-block {
			padding: 0;
			p.card-text {
				padding: 0px 0px 15px;
			}
		}
	}
	&.img-bottom {
		.card_img {
			padding-top: 25px;
		}
		.card-title {
			padding-top: 0px!important;
		}
	}
	.card-img-overlay {
		p.card-text {
			color: $white-color;
		}
	}
	&.card-inverse {
		&:before {
		    position: absolute;
		    content: '';
		    top: 0;
		    left: 0;
		    right: 0;
		    bottom: 0;
		    background: rgba(20,20,20,0.7);
		}
	}
	ul.list-inline {
		li {
			margin-right: 10px;
			&:last-child {
				margin-right: 0px;
			}
			i {
				margin-right: 5px;
			}
		}
	}
	&.text-white {
		.card-title, .card-text {
			color: $white-color;
		}
	}
	&.card-body {
		border: 1px solid $sand;
	}
	&.card-style3 {
		.card-block {
			z-index: 55;
			margin: -40px auto 0;
			background: $white-color;
			border: 1px solid transparent;
			padding: 30px;
			padding-bottom: 50px;
			width: 85%;
			@include transition(all 0.3s ease);
			
			&:before {
				bottom: 35px;
				height: 2px;
				background-color: $border-color;
			}
		}
		&:hover {
			.card-block {
				border: 1px solid $border-color;
			}
		}
	}
	//card ribbon
	.card-ribbon {
	    width: 200px;
	    padding: 2px 5px;
	    position: absolute;
	    text-align: center;
	    font-size: 12px;
	    z-index: 1;
	    &.card-ribbon-top {
	        top: 20px;
	        &.card-ribbon-left {
			    -webkit-transform: rotate(-45deg);
			    -ms-transform: rotate(-45deg);
			    transform: rotate(-45deg);
			}
	        &.card-ribbon-right {
			    -webkit-transform: rotate(45deg);
			    -ms-transform: rotate(45deg);
			    transform: rotate(45deg);
			}
	    }
	    &.card-ribbon-bottom {
	        bottom: 20px;
	        &.card-ribbon-left {
			    -webkit-transform: rotate(45deg);
			    -ms-transform: rotate(45deg);
			    transform: rotate(45deg);
			}
	        &.card-ribbon-right {
			    -webkit-transform: rotate(-45deg);
			    -ms-transform: rotate(-45deg);
			    transform: rotate(-45deg);
			}
	    }
	    &.card-ribbon-left {
	        left: -70px;
	    }
	    &.card-ribbon-right {
		    right: -70px;
		}
	}
}
.date-holder {
    position: absolute;
    top: 15px;
    font-size: 14px;
    color: #fff;
    width: 65px;
    height: 65px;
    text-align: center;
    padding-top: 13px;
    left: 15px;
    span {
    	font-family: 'Montserrat', sans-serif;
        font-size: 22px;
        display: block;
    }
    &::after {
	    position: absolute;
	    content: '';
	    width: 0;
	    height: 0;
	    border-top: 0px solid transparent;
	    border-bottom: 20px solid transparent;
	    border-left: 15px solid $primary-color;
	    bottom: -20px;
	    left: 0;
	}
}
