/*=== 1.COMMON ===*/
/* ===Font faces */

@import url('https://fonts.googleapis.com/css?family=Lato|Montserrat|Open+Sans|PT+Serif|Poppins|Raleway');
.container{
	position:relative;
	width: 100%;
	@include mobile {
		width: 450px;
	}
	@include tablet {
		width: 750px;
	}
	@include desktops {
		width: 970px;
	}
	@include large-desktops {
		width: 1140px;
	}
}

a{
	color: $text-color;
	text-decoration: none;
	@include transition(all 0.3s ease-in-out);
	&:hover, &:focus{
		outline: 0;
		color: $cornflower-blue;
		text-decoration: none;
	}
}
.main-wrapper {
	overflow-x: hidden;
}
.boxed {
	.main-nav {
		@include tablet {
			box-shadow: none;
		}
	}
	.main-wrapper {
		position: relative;
		background-color: $white;
		margin: 0 auto;
		overflow-x: hidden;
		@include mobile {
			max-width: 520px;
		}
		@include tablet {
			padding: 0px 0 0;
			max-width: 728px;
		}
		@include desktops {
			padding: 0px 0 0;
			max-width: 970px;
		}
		@include large-desktops {
			padding: 0px 0 0;
			max-width: 1260px;
		}
	}
	.home_transparent-wrapper {
		&.main-wrapper {
			padding-top: 0px;
		}
	}
	.navbar-sticky {
		@include mobile {
			max-width: 520px;
			left: 50%;
			@include transform(translateX(-50%));
		}
		@include tablet {
			padding: 25px 0 0;
			max-width: 728px;
		}
		@include desktops {
			padding: 80px 0 0;
			max-width: 970px;
		}
		@include large-desktops {
			padding: 80px 0 0;
			max-width: 1260px;
		}
	}
}


.visible {
	display: block!important;
}
.form-control:focus{
	box-shadow: none;
}
.btn-cta {
	color: $title-color!important;
	background-color: $white;
}



.img-full {
	width: 100%;
}
.img-circle {
	position: relative;
	border-radius: 50%;
}
.bg-image-holder, .bg-image {
	background-size: cover !important;
	background-position: 50% 50%;
	@include transition(all 0.3s linear);
	background: $sand;
	position: relative;
}
::selection {
	background-color: $primary-color;
	color: $white;
}

/*=== 1.1  ALL BACKGROUND COLORS ===*/ 
.bg-br {
	background-color: $black-russian;
}
.bg-primary {
	background-color: $primary-color!important;
}

.bg-ghost-white{
	background-color:$ghost-white; 
}
.bg-ebony{
	background-color: $ebony;
}
.bg-dark-navy {
	background-color: $dark-navy!important;
}
.no-bg {
	background-color: transparent;
}

.primary-color{
	color: $primary-color;
}
.white-color{
	color: $white-color;
}
.bordered {
	border: 1px solid $border-color;
}

/*=== 1.2 CUSTOM MARGIN CLASSES ===*/
.mb30 {
	margin-bottom: 30px;
}

.mt-45{
	margin-top: 35px;
	@include tablet{
		margin-top: 45px;
	}
}
.m-tb-35{
	margin: 20px 0;
}
.mb-50{
	margin-bottom: 50px;
}

.spacing45{
	margin-top: 35px;
		@include tablet{
			margin-top: 0;
		}
		@include large-desktops{
			margin-left: 12px;
			margin-top: 0;
		}
	}
/*=== 1.3 CUSTOM PADDING CLASSES ===*/
.no-padding {
	padding: 0!important;
}
.pv100 {
	padding-top: 80px;
	padding-bottom: 80px;
	@include desktops{
		padding-top: 100px;
		padding-bottom: 100px;
	}
}
.pv50{
	padding-top: 50px;
	padding-bottom: 50px;
}
.section-padding {
	padding-top: 70px;
	padding-bottom: 70px;
	@include tablet{
	padding-top: 90px;
	padding-bottom: 90px;
}
}
.pt30 {
	padding-top: 30px;
}
.pb30 {
	padding-bottom: 30px;
}
.pb50 {
	padding-bottom: 50px;
}
.pv30 {
	padding-top: 30px;
	padding-bottom: 30px;
}
.pt100{
	padding-top: 100px;
}
.pb100 {
	padding-bottom: 100px;
}
/*=== 1.4 FONT WEIGHT CLASSES ===*/ 
.bold {
	font-weight: 700!important;
}
.medium {
	font-weight: 500!important;
}

/*=== 1.5 CUSTOM MIDEA ===*/ 

.media{
    position: relative;
    .media-body{
        background: #fff;
        .media-date{
            font-family: 'Open Sans', sans-serif;
            color: #848484;
        }
        h5{
            font-size: 22px;
            &.small{
                font-size: 15px;
            }
        }

        p{
            font-family: 'Open Sans', sans-serif;
            color: #848484;
        }
    }
}




/*=== 1.6 SIDE MENU ELEMENTS ===*/ 

.side-menu {
	.side-menu-margin{
		margin-bottom: 50px;
		@include desktops{
			margin-bottom: 0;
		}
	}
li {
	border-bottom: 1px solid #e5e5e5;
	background-color: #fff;
	font: 600 14px 'Open Sans', sans-serif;
	a{
		padding: 15px 0;
		color: $gray;
		transition: .4s;
		width: 100%;
		&:hover{
			color: $cornflower-blue;
			i.fa {
				margin-right: 20px;
				color: $cornflower-blue;
			}
		}
		i.fa {
			margin-right: 15px;
			transition: .4s;
		}
	}
	}
}
.sidebar-search{
    position: relative;
    margin-bottom: 20px;
    input{
        width: 100%;
        padding: 13px 15px;
		border: 1px solid #e5e5e5;
		@include placeholder{color: #848484}
    }
    .btn-search{
        top: 13px;
    }
}

.tag-list{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 25px;
    .common-btn{
        &.tag-btn{    
            padding: 13px 10px;
            font-size: 12px;
            margin-right: 10px;
            margin-top: 10px;
            line-height: 13px;
        }
    }
}
/*=== 1.7 Hover Overlays ===*/
.hover-overlay {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	a.overlay_icon {
		width: 55px;
		height: 55px;
		background: #fff;
		position: relative;
		display: inline-block;
		border-radius: 50%;
		margin: 3px;
		text-align: center;
		i.fa {
			font-size: 20px;
			line-height: 50px;
			color: $text-color;
		}
	}
}

/*=== 1.7 HOVER OVERLAY EFFECTS ===*/
.hover-overlay {
	&.effect-transformY {
		position: absolute;
		content: '';
		background-color: rgba($primary-color, 0.8);
		left: 0;
		right: 0;
		bottom: 0;
		height: 30%;
		@include transition(transform 0.3s ease);
		@include large-desktops{
			@include transform(translateY(100%));
			height: 100%;
		}
	}
	&.effect-scale {
		@include transition(all 0.4s ease);
			-webkit-transform: scale(1);
			transform: scale(1);
			opacity: 1;
			height: 30%;
		@include large-desktops{
			@include transition(all 0.4s ease);
			-webkit-transform: scale(0);
			transform: scale(0);
			opacity: 0;
			height: auto;
		}
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			opacity: 0;
			-webkit-transform: scale(0);
			transform: scale(0);
			-webkit-transition: -webkit-transform 0.4s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.4s;
			transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.4s;
			background: rgba($primary-color,0.7);
		}
	}
}
.card, .card_img {
	&:hover {
		.hover-overlay.effect-transformY {
			@include transform(translateY(0));
		}
		.hover-overlay.effect-scale {
			-webkit-transform: scale(1);
			transform: scale(1);
			opacity: 1;
			height: 30%;
			@include large-desktops{
				height: 100%;
			}
			&:before {
				opacity: 1;
				-webkit-transform: scale(1);
				transform: scale(1);
			}
		}
	}
}
.img_overlay-container {
	position: relative;
	padding: 0 30px;
	.img_overlay-title {
		color: $white-color;
		margin-bottom: 10px;
	}
	.img_overlay-desc {
		color: tint($white-color, 70%);
		margin-bottom: 20px;
	}
	a.icon {
		width: 38px;
		height: 38px;
		font-size: 15px;
		line-height: 36px;
		background-color: transparent;
		text-align: center;
		display: inline-block;
		color: $white-color;
		border: 1px solid $white-color;
		margin-right: 5px;
		&:hover {
			background-color: $primary-color;
			border-color: $primary-color;
		}
	}
}


/*=== 1.9 PRIMARY OVERLAYS ====*/
.primary-overlay,.custom-overlay{
	position: relative;
	&:before{
		background-color: $primary-color;
		position: absolute;
		content: "";
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		opacity: .82;
	}
}
  
/*=== 1.10 COMMON BUTTON ===*/
.common-btn{
	color: $black;
	background-color:$white;
	font:700 14px 'Open Sans', sans-serif;
	height:50px;
	line-height: 50px;
	padding: 0 35px;
	text-transform: uppercase;
	border: 1px solid transparent;
	&.blue{
		background-color:$blue;
		color:$white;
		&:hover{
			background-color:transparent;
			color: $primary-color;
			border: 1px solid #e5e5e5; 
		} 
	}
	&.btn-transparent{
		color: $primary-color;
		background-color: transparent;
		border: 1px solid #e5e5e5;
		&:hover{
			background-color:$primary-color;
			color: $white-color;
			border-color:$primary-color;  
		}
	}
	&.color-gray{
		color: $gray;
	}
}

/* 1.11 SECTION TITLES  */ 
.section-title-md {
    font-size: 36px;
}
.section-title-sm {
	font-size: 24px;
	margin-bottom: 18px;
}

.section-title-md, .section-title-sm{
	font-family: 'PT Serif', serif;
}

/* 1.12 SECTION TITLE SEPERATOR */ 
.sep{
	position: relative;
	padding-bottom: 30px;
	&:before{
		position: absolute;
		background-color:$blue;
		content: "";
		width: 50px;
		height: 2px;
		left: 0;
		bottom: 0;

	}
}
.border-gray{
	border-color:$gray-dark; 
}
.highlighter {
	background: #282C34;
	padding: 0px 10px;
	border-radius: 4px;
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace !important;
	display: inline-block;
}


/* 1.13 INSTAFEED ZOOM EFFECT  */ 

.mfp-zoom-in {

    /* start state */
    .mfp-with-anim {
      opacity: 0;
      transition: all 0.2s ease-in-out; 
      transform: scale(0.8); 
    }
    
    &.mfp-bg {
      opacity: 0;
        transition: all 0.3s ease-out;
    }
    
    /* animate in */
    &.mfp-ready {
      .mfp-with-anim {
        opacity: 1;
        transform: scale(1); 
      }
      &.mfp-bg {
        opacity: 0.8;
      }
    }
    
    /* animate out */
    &.mfp-removing {
      
      .mfp-with-anim {
        transform: scale(0.8); 
        opacity: 0;
      }
      &.mfp-bg {
        opacity: 0;
      }
      
    }
    
  }



