header{
    .main-nav {
        left: 0%;
        top: 0;
        transform: translateX(0%);
        width: 100%;
        padding: 2.5px 0;
        z-index: 9999;
        @include tablet{
            left: 50%;
            top: 0;
            transform: translateX(-50%);
            width: 100%;
            padding: 20px 0;
            z-index: 9999; 
        }
        .navbar-brand{
            .logo-color{
                fill:$white-color;
            }
        }
        .burger-menu{
            i{
                color: #fff;
            }

        }
        &.normal-nav{
            position: relative;
        }
    }
    ul.navbar-nav{
        li{
            @include tablet{
                margin: 10px 6px;
            }
            a{
                color: $primary-color;
                font:700 15px 'Open Sans', sans-serif;
                @include tablet{
                    color: $white;
                }
                &:hover{
                    color: $primary-color;
                }
            }
        }
    }
    form.search_form {
        margin-top: 5px;
        position: relative;
        right: 0;
        background: #f5f5f6;
        top: 80%;
        z-index: 10;
        padding: 20px 30px;
        border-top: 1px solid #ccc;
        box-shadow: 2px 3px 9px rgba(44,40,86,.1);
        @include tablet{
            display: none;
        }

    }
}
.navbar-sticky{
    .logo-color{
        fill:$primary-color;
    }
    ul.navbar-nav{
        li{
            @include tablet{
                margin: 10px 9px;
            }
            @include desktops{
                margin: 10px 5px;
            }
            a{
                font:700 15px 'Open Sans', sans-serif;
            }
            ul{
                margin:0;
            }
        }
        
    }
}
