/* ================================================
--> Kit Gallery style
================================================== */
.gallary-img.card_img{
    &:hover{
        .description-gallary{
            opacity: 1;
        }
	}
	img{
		display: block;
		width: 100%;
	}
}
.hover-overlay {
	justify-content: flex-start;
	padding-left: 20px;
	&.effect-scale {
		z-index: 5;
		&:before {
			top: 0px;
			left: 0px;
			right: 0px;
			bottom: 0px;
			border-radius: 2px;
			opacity: 1;
			z-index: -1;
			@include transform(scale(1));
			@include large-desktops {
				opacity: 0;
				@include transform(scale(0));
			}
		}
		&:before {
			background-color: rgba($primary-color,0.80);
		}
	}
	.overlay-content {
		font-weight: 300;
		span {
			font-size: 12px;
			font-weight: 400;
			text-transform: uppercase;
			letter-spacing: 1px;
		}
		@include large-desktops{
			position: absolute;
			display: block;
			left: 20px;
			bottom: 20px;
		}
	}
	// Rectangle Icon style
	.icon-ract {
		border: 1px solid tint($white-color, 70%);
		width: 40px;
		height: 40px;
		display: inline-block;
		border-radius: 4px;
		line-height: 40px;
		margin: 15px 8px;
		i {
			color: $white-color;
			font-weight: 300;
		}
		&:before {
			width: 40px;
			height: 40px;
			background: transparent; 
			left: -1px;
			top: -1px;
			border-radius: 4px;
		}
		&:hover{
			border: 1px solid $white-color!important;
			i {
				color: $primary-color;
			}
			&:before {
				background: $white-color;
			}
		}
	}
	// Circle Icon style
	
    
    .hover-btn {
        display: none;
        @include large-desktops{
            text-align: center;
            display: block;
            position: absolute;
            top: 35%;
            left: 50%;
            transform: translateX(-50%); 
            width: 100%;
            .icon-circle {
                width: 50px;
                height: 50px;
                border: 1px solid tint($white-color, 70%);
                border-radius: 100%;
                line-height: 48px;
                color: $white-color;
                text-align: center;
                &:hover {
                    background: $white-color;
                    color: $primary-color;
                }
            }
        }
	}  
	.overlay-title {
		color: $white-color;
		margin-bottom: 5px;
		font-weight: 400;
    }
    .description-gallary {
        
        @include large-desktops{
			position: absolute;
        	bottom: 0;
			opacity: 0;
			margin: 25px;
		}
    }
    .card-title,.secondery-title{
        color: $white;
    }
    .card-title{
        margin-bottom: 0px;
        font-size: 26px;
        @include large-desktops{
            margin-bottom: 5px;
        }
    }
    .secondery-title{
        font-style: italic;
    }
}

