/* About CSS */ 
.about-section{
    .text-block{
        .small-heading{
            margin-bottom: 12px;
        }
    }
}
.img-pattern-style {
    position: relative;
    padding-left: 15px;
    padding-bottom: 16px;
    @include desktops{
        padding-left: 30px;
        padding-bottom: 28px;
    }
    &:before {
        background-image: url(../img/home-page/about-sec-pattern.jpg);
        position: absolute;
        content: "";
        left: 0px;
        height: 90%;
        width: 91%;
        z-index: -1;
        bottom: 0px;
    }
    img{
        box-shadow: 6px 3px 14px 0px rgba(0, 0, 0, 0.22)
    }
    &.flip{
        padding-left: 0;
        padding-right: 15px;
        img{
            box-shadow: -6px 3px 14px 0px rgba(0, 0, 0, 0.22);
        }
        &:before{
            left: auto;
            right: -1px;
        }
    }
}
